:root, :host {
  --md-sys-color-background: #17130d;
  --md-sys-color-on-background: #ebe1d6;
  --md-sys-color-surface: #17130d;
  --md-sys-color-on-surface: #ebe1d6;
  --md-sys-color-primary: #f1bf60;
  --md-sys-color-on-primary: #412d00;
  --md-sys-color-surface-variant: #4f4537;
}

.light-theme {
  --md-sys-color-background: #fff;
  --md-sys-color-on-background: #000;
  --md-sys-color-surface: #fff;
  --md-sys-color-on-surface: #000;
  --md-sys-color-primary: #6200ee;
  --md-sys-color-on-primary: #fff;
  --md-sys-color-surface-variant: #e0e0e0;
}

body {
  font-family: var(--md-sys-typescale-body1-font-family, "Roboto", sans-serif);
  background-color: var(--md-sys-color-background);
  color: var(--md-sys-color-on-background);
  margin: 0;
}

.navbar {
  padding: var(--md-sys-spacing-3, 16px);
  background-color: var(--md-sys-color-surface);
  color: var(--md-sys-color-on-surface);
  z-index: 1000;
  width: 100%;
  box-shadow: var(--md-sys-elevation-level1);
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
}

.navbar-logo {
  align-items: center;
  display: flex;
}

.logo-image {
  object-fit: contain;
  width: auto;
  height: 50px;
  margin: 0;
}

.navbar-links {
  display: flex;
}

md-tabs {
  --md-tabs-active-indicator-color: var(--md-sys-color-primary);
}

md-tab {
  --md-comp-tab-label-text-style: var(--md-sys-typescale-title-medium);
  color: var(--md-sys-color-on-surface);
  cursor: pointer;
}

md-tab:hover {
  background-color: var(--md-sys-color-surface-variant);
}

md-tab[active] {
  color: var(--md-sys-color-primary);
  font-weight: bold;
}

md-tab::part(active-indicator) {
  background-color: var(--md-sys-color-primary);
}

#toggle-theme {
  color: var(--md-sys-color-on-surface);
}

.section {
  padding: var(--md-sys-spacing-5, 32px);
  padding-top: 100px;
  display: none;
}

.section.active {
  display: block;
}

h1 {
  font-size: var(--md-sys-typescale-headline-large-font-size, 32px);
  font-weight: var(--md-sys-typescale-headline-large-font-weight, 400);
}

p {
  font-size: var(--md-sys-typescale-body-large-font-size, 16px);
  font-weight: var(--md-sys-typescale-body-large-font-weight, 400);
}

.footer {
  text-align: center;
  padding: var(--md-sys-spacing-3, 16px);
  background-color: var(--md-sys-color-surface);
  color: var(--md-sys-color-on-surface);
}

@media (width <= 768px) {
  .navbar-links {
    display: none;
  }
}
